body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* FLEX */
.flex { display: flex; }

.horizontal { flex-direction: row; }
.vertical { flex-direction: column; }
@media screen and (max-width: 720px){
  .horizontal { flex-direction: column; }
}

.flex-1 { flex: 1; }
.flex-2 { flex: 2; }
.flex-3 { flex: 3; }
.flex-4 { flex: 4; }
.flex-5 { flex: 5; }
.flex-6 { flex: 6; }

.flex-center {
  justify-content: center;
  align-items: center;
}
.flex-end {
  justify-content: flex-end;
  align-items: center;
}
.flex-start {
  justify-content: flex-start;
  align-items: center;
}

/* ATOMS */
.container {
  width: calc(100% - 20px);
  padding: 0 10px;
  max-width: 1200px;
  margin: 0 auto;
}

/* COMPONENTES */
.TopBar {
  background-color: rgb(165, 43, 122);
}
.item-topbar i {
  padding: 0 3px;
  font-size: 1rem;
}
.item-topbar, .item-topbar-right {
  margin: 5px 0;
  padding: 0 8px;
  color: #FFF;
  border-right: 1px solid #FFF;
  font-size: 0.9rem;
}
.item-topbar-right {
  border-right: 0;
  border-left: 1px solid #FFF;
}

.logo {
  padding: 10px 0;
  max-width: 120px; }
.input-pesquisa {
  height: 30px;
  width: 100%;
  padding: 5px 10px;
  border-radius: 5px;
  border: 1px solid #ddd;
  color: #222;
  font-size: 1rem;
  margin-left: 30px; }
.button-pesquisa {
  margin-left: -40px;
  font-size: 1.3rem;
  color: #CB57A2;
  background-color: transparent;
  cursor: pointer;
  border: 0; }
.item-menu {
  color: #CB57A2;
  font-size: 1.2rem;
  cursor: pointer; }
.item-menu i {
  padding: 0 10px; }
.item-menu span {
  font-size: 0.8rem; }
.item-cart {
  font-size: 1.5rem;
}
.item-cart span {
  margin-left: -15px;
  margin-top: -20px;
  background-color: #CB57A2;
  color: #FFF;
  font-size: 1.2rem;
  border-radius: 50px;
  width: 18px;
  padding-left: 6px;
}
.Header {
  padding: 5px 0;
}

.Categorias {
  background-color: #CB57A2;
}
.Categorias > div > div {
  justify-content: center;
  align-items: center;
  display: flex;
}
.item-categoria {
  padding: 5px 6px;
  margin: 5px 0;
  color: #fff;
  max-width: 120px;
  font-weight: bold;
  text-align: center;
  border-radius: 10px;
  cursor: pointer;
}
.item-categoria:hover {
  background-color: rgb(165, 43, 122);
}
.outlet {
  color: rgb(119, 7, 80);
}
.no-mb { display: block; }
.only-mb-flex { display: none; }
.no-mb-flex { display: flex; }
@media screen and (max-width: 720px){
  .logo { width: 100px; }
  .overflow-x { overflow-x: scroll; }
  .no-mb { display: none; }
  .input-pesquisa { margin-left: 0; height: 30px; }
  .button-pesquisa { margin-top: -10px; }
  .item-menu { margin: 15px 0; }
  .only-mb-flex { display: flex; }
  .Categorias > div > div { justify-content: flex-start; }
  .input-pesquisa { margin-bottom: 10px; }
  .titulo { margin-top: 20px; }
  .no-mb-flex { display: none; }
}

.breadcrumbs {
  padding: 20px 0;
  color: #777;
  font-size: 0.8rem;
}
.img-mini-container {
  cursor: pointer;
}
.detalhes-produto .titulo h2 {
  margin-top: -5px;
}
.detalhes-produto .preco .preco-crossed,
.detalhes-produto .preco .preco-off {
  margin: 0;
}
.detalhes-produto .preco .preco-crossed {
  color: #555;
  text-decoration: line-through;
}
.detalhes-produto .preco .preco-off {
  font-size: 1.6rem;
}
.detalhes-produto .preco .tag-promo {
  background-color: rgb(27, 114, 19);
  padding: 3px 10px;
  color: #FFF;
  font-size: 0.8rem;
  border: 0;
}
.detalhes-produto .preco {
  margin: 10px 0 20px;
}
.opcao > span, .quantidade-item span {
  font-size: 0.9rem;
  color: #373737;
}
.tam {
  border: 1px solid #aaaa;
  padding: 5px 8px;
  margin: 5px 15px 0 0;
  cursor: pointer;
}
.tam-active, .tam:hover {
  border: 0;
  background-color: #aaaa;
}
.quantidade {
  margin: 10px 0;
}
.quantidade-item input {
  font-size: 1rem;
  width: 30px;
  padding: 5px 8px;
}
.quantidade-item button {
  margin: 10px 0;
  padding: 0px 10px;
  color: #FFF;
  background-color: #171717;
  border: 0;
  width: 10px;
  cursor: pointer;
}
.disponibilidade {
  color: #0d7015;
  font-weight: bold;
}
.btn-cta {
  background-color: rgb(207, 27, 27);
  border: 0;
  padding: 15px 20px;
  color: #FFF;
  font-weight: bold;
  font-size: 1.4rem;
}
.hr {
  margin: 30px 0;
  height: 2px;
  background-color: #9999;
}
.input-cep {
  padding: 10px 15px;
  border-radius: 10px 0 0 10px;
  border: 1px solid #9999;
  font-size: 0.85rem;
}
.button-cep {
  border-radius: 0 10px 10px 0;
  padding: 11px 15px;
  font-size: 0.85rem;
  border: 0;
  background-color: #474747;
  color: #FFF;
}
.aviso-frete{
  padding: 20px 0;
  color: #333;
  font-weight: bold;
}
.aviso-frete i {
  padding-right: 10px;
}

.Descricao {
  margin: 50px 0 20px;
}
.Descricao > div {
  border-left: 2px solid #CB57A2;
  padding-left: 10px;
  padding-bottom: 20px;
}
.table {
  border-collapse: collapse;
}
.table td {
  padding: 8px 12px;
}
.table tr td:first-child {
  background-color: #CB57A2;
  color: #FFF;
  font-weight: bold;
}
.table tr td:last-child {
  background-color: #D39CC2;
  color: #171717;
}

.ProdutosRelacionados {
  padding: 20px 0;
}
.ProdutosRelacionados h3 {
  font-size: 1.5rem;
  padding: 0 15px;
}
.produto-rel {
  padding: 20px 10px;
}
.produto-rel-titulo {
  height: 90px;
}
.produto-rel-titulo h2 {
  font-size: 1.2rem;
  padding: 0 10px;
}
.produto-rel-preco {
  padding: 10px 0;
}
.produto-rel-preco small {
  text-decoration: line-through;
}
.produto-rel-preco h3 { 
  margin: 0; 
  font-size: 1.4rem;
}

.Novidades {
  background-color: #CB57A2;
  padding: 20px 0;
}
.Novidades h3 {
  font-size: 2rem;
  color: #FEFEFE;
}
.input-newsletter {
  height: 20px;
  padding: 10px 15px;
  border-radius: 10px 0 0 10px;
  border: 0;
  width: 200px;
  font-size: 1rem;
}
.button-newsletter {
  padding: 10px 15px;
  color: #FEFEFE;
  background-color: #222;
  border: 0;
  border-radius: 0 10px 10px 0;
  font-size: 1rem;
  font-weight: bold;
}

.Institucional {
  background-color: #333;
  padding: 20px 5px;
}
.Institucional h2 {
  font-size: 1.2rem;
  color: rgb(243, 91, 190);
}
.Institucional span, .Institucional i {
  color: #eee;
  line-height: 20px;
  padding: 5px;
}
.Institucional i {
  font-size: 1.7rem;
}
.item-institucional {
  padding: 0 10px; 
  font-size: 0.9rem;
}

.Footer {
  padding: 10px 10px;
  background-color: #222;
  color: #FEFEFE;
}

@media screen and (max-width: 720px){
  .btn-cta { width: 100%; }
  .input-cep { width: 150px; }
  .Descricao { margin-left: 5px; }
  .input-newsletter { width: 170px; padding: 8.5px 5px; }
  .button-newsletter { font-size: 0.85rem; }
  .Categorias {
    margin: 0 -10px;
  }
}